import Commodity from "./Commodity";
import { setPid, getGroups, getProducts, setViewForm, getProductId, setFormData } from "../../redux/commodityReduser";
import { connect } from "react-redux";

const mapState = state => {
  state = state.commodityPage;
  return {
    isLoaded: state.isLoaded,
    groups: state.groups,
    price: "Price",
    pid: state.pid,
    commodities: state.commodities,
    comIsLoaded: state.comIsLoaded,
    error: state.error,
    viewForm: state.viewForm,
    formData: state.formData
  };
};

const CommodityContainer = connect(mapState, { getGroups, getProducts, setPid, setViewForm, getProductId, setFormData })(Commodity);

export default CommodityContainer;
